@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(rgb(175, 103, 233), rgb(101, 101, 231));
  background-attachment: fixed;
  background-repeat: no-repeat;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

p {
  font-size: 14px;
  font-family: "Kumbh Sans", sans-serif;
  font-weight: 400;
  margin-bottom: 0;
}

.contenedor {
  width: 90%;
  max-width: 90rem;
  height: 100%;
  background-color: white;
  margin: auto;
  margin-top: 13rem;
  margin-bottom: 4rem;
  padding: 3rem;
  border-radius: 3rem;
  box-shadow: 0px 20px 12px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .contenedor {
    display: flex;
    align-items: center;
    margin-bottom: 0;
  }
}
@media (min-width: 1020px) {
  .contenedor .contenedor {
    display: flex;
    align-items: center;
    gap: 7rem;
    height: 47rem;
    margin-bottom: 0;
  }
}

h1 {
  font-family: "Kumbh Sans", sans-serif;
  text-align: center;
  font-size: 4rem;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  h1 {
    text-align: left;
    margin-bottom: 2rem;
    font-size: 3rem;
  }
}
@media (min-width: 1020px) {
  h1 {
    text-align: left;
    margin-bottom: 2rem;
    font-size: 3rem;
  }
}

.senora {
  background-image: url(/build/img/illustration-woman-online-mobile.svg);
  height: 20rem;
  width: 28rem;
  margin: auto;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: relative;
  bottom: 15rem;
  right: 0.5rem;
  margin-bottom: -12rem;
}
@media (min-width: 768px) {
  .senora {
    position: relative;
    margin: auto;
    right: 2rem;
    top: 0.5rem;
  }
}
@media (min-width: 1020px) {
  .senora {
    background: url(/build/img/illustration-woman-online-desktop.svg);
    height: 36rem;
    width: 39rem;
    background-position: right;
    background-repeat: no-repeat;
    position: relative;
    margin: 0;
    bottom: 0;
    right: 3rem;
  }
}
.senora .sombra {
  background-image: url(/build/img/bg-pattern-mobile.svg);
  height: 12rem;
  width: 28rem;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  bottom: -3rem;
}
@media (min-width: 1020px) {
  .senora .sombra {
    background: url(/build/img/bg-pattern-mobile.svg);
    width: 40rem;
    height: 35rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    top: 17rem;
  }
}
@media (min-width: 1020px) {
  .senora .cubo {
    background: url(/build/img/illustration-box-desktop.svg);
    background-position: center top;
    width: 20rem;
    height: 16rem;
    position: absolute;
    left: -10rem;
    bottom: 6rem;
  }
}

.flecha {
  display: flex;
  justify-content: space-between;
  align-items: first baseline;
}

.respuesta {
  display: none;
}

@media (min-width: 768px) {
  .pregunta {
    width: 35rem;
  }
}
@media (min-width: 1020px) {
  .pregunta {
    width: 38rem;
  }
}

.linea-separacion {
  margin-top: 2rem;
  background-color: rgb(231, 231, 233);
  width: 100%;
  height: 0.16rem;
}

.visible {
  display: block;
}
.visible p:first-child {
  font-weight: bold;
}